.headder{
  /* padding: 10px 0px; */
  display: flex;
  text-align: center;
  z-index: 1;
}
.search{
  background-color: rgb(227, 228, 228);
  padding: 12px;
  border: none;
  border-radius: 24px;
  margin-left: 10px;
  width: 250px;
}
.align-item {
  align-items: center;
}
.nav-list {
  display: flex;
  justify-content: center;
  font-size: 12px;
  font-weight: 600;
}
.nav-links li a {
  font-family: "poppins", sans-serif;
  text-decoration: none;
  font-size: 12px;
  text-transform: capitalize;
  list-style: none;
}
.nav-links a {
  text-decoration: none;
  color: #ffffff;
  padding: 0px 20px;
}
.nav-links.active a {
  color: #ffb424;
}
.nav-links a:hover {
  color: #ffb424;
}
ul {
  list-style-type: none;
}
.nav-bar{
  background-color: rgba(0, 0, 0, 0.644);
  padding: 20px;
  width: 113.2%;
  margin: 0px -100px;
}
.bgimg{
  width: 100%;
  height: auto;
  margin: 0px -100px;
  position: fixed;
  z-index: -1;
}
.titleBg{
  background-color: rgba(0, 0, 0, 0.644);
  padding: 20px ;
  width: 113.2%;
  margin: 0px -100px;
}
.title{
  text-align: center;
  margin-top: 100px;
  font-family: 'Satisfy', cursive;
  font-size: 60px;
  font-weight: 300;
  color: #ffffff;
}
.subtitle{
  text-align: center;
  margin-top: -40px;
  margin-bottom: 300px;
  font-weight: 400;
  font-size: 14px;
  color: #ffffff;
}
.about_me_sec{
  background-color: #ffffff;
  padding: 5px 20px 20px 20px;
}
.abot_head{
  text-align: center;
  position: relative;
}
.about_tit{
  font-size: 65px;
  color: rgba(163, 163, 163, 0.281);
}
.about_tit2{
  font-size: 38px;
  position: absolute;
  top: 10px;
  left: 42%;
}
.abot_head_sub{
  text-align: center;
}
.about_tit_sub{
  font-size: 20px;
  font-weight: 400;
}
.about_img_sec{
  display: flex;
}
.about_details_sec1{
  display: flex;
}
.about_details{
 padding-left: 30px;
}
.about_details2{
  padding-left: 200px;
}
.material-symbols-outlined{
  font-size: 12px;
  margin-right: 10px;
  color: rgb(247, 136, 57);
}
.about_work{
  display: flex;
  align-items: top;
  margin-left: 20px;
}
.smile{
  font-size: 40px;
  margin-top: 20px;
}
.about_details_sec2{
  display: flex;
}
